html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFF;
}

.container {
  max-width: 1170px;
  width: 85%;
  margin: 0 auto; 
}

.header{
  background-color: #296709;
  width: 100%;
}

.header__content{
  padding: 20px 0;
  text-align: center;
}

.content__img {
  display: inline-block;
  height: 100px;
}

.footer__links-container{
  color: #989898;
  text-align: center;
  padding-top: 20px;
}

.links__container{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 10px 0;
}

.contacts__link{
  margin: 10px;
}

.contacts__link:hover{
  color: #fff;
}

.footer{
  background-color: #3C3C3C;
  width: 100%;
  height: 100px;
  clear: both;
  position: relative;
}

.events-section__content {
  width: 300px;
}