/* The container */
.label__control {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

/* Hide the browser's default checkbox */
.check__input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

/* Create a custom checkbox */
.check__box {
  position: absolute;
  top: 4px;
  border-radius: 50%;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee; }

/* On mouse-over, add a grey background color */
.label__control:hover .check__input ~ .check__box {
  background-color: #ccc; }

/* When the checkbox is checked, add a blue background */
.label__control .check__input:checked ~ .check__box {
  background-color: #2196F3; }

/* Create the checkmark/indicator (hidden when not checked) */
.check__box:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.label__control .check__input:checked ~ .check__box:after {
  display: block; }

/* Style the checkmark/indicator */
.label__control .check__box:after {
  left: 7px;
  top: 3px;
  width: 5px;
  height: 8px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.event-section {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 10px; }

.event-section__event-day {
  margin: 20px 0; }

.event-section__event-day .event-day__event-info {
  margin-left: 20px;
  width: 90px; }

.event-section__event-day_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 130px; }

.event-info__content {
  font-style: italic;
  color: #BFC0C6;
  font-size: 14px; }

.event-day__event-info p {
  text-align: center; }

.event-day_container__main-info {
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  background-color: greenyellow;
  color: #fff; }

.main-info__additional {
  display: none; }

.visible.popup {
  display: block; }

.more-info__link {
  color: rgba(255, 253, 253, 0.925);
  font-size: 13px;
  font-style: italic;
  cursor: pointer; }

.popup {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.7); }

body .popup {
  height: 100vh;
  overflow-y: hidden; }

.popup-content {
  margin: 15% auto;
  overflow-y: auto;
  text-align: center;
  height: 550px;
  padding: 20px;
  color: #fff;
  width: 80%;
  position: relative; }

.popup-content__img {
  object-fit: cover;
  height: 256px;
  width: 256px;
  margin: 20px 0; }

.close {
  color: #aaa;
  top: -7px;
  right: 20px;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  position: absolute; }

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer; }

.loading__animation-elements-container {
  background-color: #fff;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 100px auto;
  -webkit-animation: blinker 2s linear infinite;
          animation: blinker 2s linear infinite;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end; }

.loading__animation-element {
  background-color: #000;
  margin: 8px;
  width: 15px; }

.last-element {
  height: 120px; }

.central-element {
  height: 200px; }

.main-element {
  height: 120px;
  align-self: flex-start; }

@-webkit-keyframes blinker {
  50% {
    opacity: 0; } }

@keyframes blinker {
  50% {
    opacity: 0; } }

.request-field__massage {
  text-align: center;
  font-size: 50px; }

.events-section__event-list-container,
.events-section__first-day-events-list {
