.event-section {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 10px;
}

.event-section__event-day {
  margin: 20px 0;
}

.event-section__event-day .event-day__event-info {
  margin-left: 20px;
  width: 90px;
}

.event-section__event-day_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 130px; 
}

.event-info__content {
  font-style: italic;
  color: #BFC0C6;
  font-size: 14px;
}

.event-day__event-info p {
  text-align: center;
}

.event-day_container__main-info {
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  background-color: greenyellow;
  color: #fff;
} 

.main-info__additional {
  display: none;
}

.visible.popup{
  display: block;
}

.more-info__link {
  color: rgba(255, 253, 253, 0.925);
  font-size: 13px;
  font-style: italic;
  cursor: pointer;
}

.popup {
  display: none; 
  position: fixed; 
  z-index: 1; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.7);
}

body .popup {
  height: 100vh;
  overflow-y: hidden;
}

.popup-content {
  margin: 15% auto;
  overflow-y: auto;
  text-align: center;
  height: 550px;
  padding: 20px;
  color: #fff;
  width: 80%;
  position: relative;
}

.popup-content__img {
  object-fit: cover;
  height: 256px;
  width: 256px;
  margin: 20px 0;
}

.close {
  color: #aaa;
  top: -7px;
  right: 20px;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  position: absolute;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
