/* The container */
.label__control {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.check__input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.check__box {
  position: absolute;
  top: 4px;
  border-radius: 50%;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
}

/* On mouse-over, add a grey background color */
.label__control:hover .check__input ~ .check__box {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.label__control .check__input:checked ~ .check__box {
  background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.check__box:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.label__control .check__input:checked ~ .check__box:after {
  display: block;
}

/* Style the checkmark/indicator */
.label__control .check__box:after {
  left: 7px;
  top: 3px;
  width: 5px;
  height: 8px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
